










module.exports =
  default:
    middleware: 'test'
    data: (req)->
      now = new Date() - 0
      clicks: 0
      name: if req then 'server' else 'client'
      times: [[-3610..-3590]...,Infinity,[-70..-50]...,Infinity,[50..70]...,Infinity,[3590..3610]...].map (t)->
        at: now - t * 1000
    head: ->
      title: "About Page (#{this.name}-side)"
    methods:
      reset: (idx)->
        @clicks++
        @times[idx].at = new Date() - -62000



